/* Компонент для отображения автарки пользователя. Т.к. компонент вызывается в
других компонентах, то нужны разные стили для автарок. Поэтому испольуем :class
*/
<template>
  <button
    class="avatar__container"
    :class="isProfile ? 'profile-container' : ''"
    @click="onPress"
  >
    <div :class="isProfile ? 'profile__img' : 'avatar__img'">
      <img :src="userStory.pic" />
    </div>
    <div v-if="!isProfile" class="avatar__name">{{ userStory.name }}</div>
  </button>
</template>
<script>
export default {
  name: "avatar-component",
  props: {
    userStory: {
      type: Object,
      required: true,
    },
    // когда вызываем автарку для профиля
    isProfile: {
      type: Boolean,
      required: false,
    },
  },
  methods: {
    onPress() {
      this.$emit("onPressUserStory");
    },
  },
};
</script>
<style scoped>
.avatar__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 30px;
  /* обнуление св-в кнопки */
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}

.profile-container {
  margin-right: 0;
}

.avatar__img {
  border: 2px solid #a6328d;
  border-radius: 50%;
  padding: 5px;
  /* width: 90px; */
}
.avatar__img img {
  width: 80px;
}

.profile__img img {
  width: 37px;
}

.avatar__name {
  font-size: 12px;
}
</style>
